@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400&display=swap);
:root {
  --primary-color: #1769aa;
  --accent-color: #00e5ff ;
  --background: #D6D4D4;
  --background-contrast: #111111; 
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.15rem;
  background-color: "D6D4D4";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{
    position: absolute;
    font-size: 6rem;
    top: 20%;
    left: 50%;
    transform:translate(-50%,-50%);
    color: #1769aa;
}

.Authentication{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.App { 
    height: 100vh;
}
.dashboard_container{
    height: 89vh;
    padding: 1rem;
}

.chart_container {
    width: 70%;
    height: 50%;
}
.nav-bar {
    padding: 1.5rem;
    background-color: var(--primary-color);
    color: white;
}

.nav-bar nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.nav-elements {
    display: flex;
    padding: 0;
    margin: 0;
}

.nav-elements li {
    list-style-type: none;
    margin: 0rem 1rem;
    cursor: pointer;
}

.nav-bar .nav-logo{
    margin: 0;
}
.form {
    position: relative;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.form .outlined-basic { 
    margin: 0.5rem;
    width: 20vw;
    min-width: 150px;
}

.question { 
    display: flex;
    flex-direction: column;
    padding: 3rem;
    justify-content: center;
    align-items: center;
    max-height: 35vh;
    /* margin: 2rem; */
    border-radius: 1rem;
    box-shadow: 5px 5px 30px 5px rgba(168, 166, 166, 0.24);
}

.description, .description ~ em {
    font-size: 1rem;
    margin: .15rem;
}

.profile-questionnaire {
    height: 80vh;
}

.disclaimer {
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translate(-50%);
    font-style: oblique;
    font-size: 0.9rem;
}

.risk {
    display: flex;
    flex-direction: row;
    margin: 0rem 5rem;
    padding: 2rem;
    height: 40vh;
    overflow: auto;
    border-radius: 1rem;
    box-shadow: 5px 5px 30px 5px rgba(168, 166, 166, 0.64);
}

.risk > div {
    width: 50%;
}

.action-buttons {
    margin: 1rem;
}
.profile {
    display: flex;
    flex-direction: column;
    padding: 5rem;
    margin: 5rem 15vw;
    border-radius: 1rem;
    box-shadow: 5px 5px 30px 5px rgba(168, 166, 166, 0.24);
}

.personal-info {
    display: flex;
    align-items: flex-end;
}

.personal-info div {
    margin: 1rem;
}

.profile img {
    margin: 1rem;
    border-radius: 1rem;
    box-shadow: 5px 5px 30px 5px rgba(168, 166, 166, 0.24);
}

.profile p{
    margin:0;
}
.profile h4 {
    margin:0;
    margin-top: 1rem;
}
